.app {
    height:100vh;

    .sections {
        width:100%;
        height:calc(100vh - 70px);
        background-color: whitesmoke;
        position:relative;
        top:70px;
        scroll-snap-type: y mandatory;
        scroll-behavior: smooth;
        scrollbar-width: none;//firefox
        &::-webkit-scrollbar {
            display: none;
        }
        
        > * {
            width:100vw;
            height:calc(100vh - 70px);
            scroll-snap-align: start;
        }

    }
}